<template>
  <div class="car-edit-dialog">
    <el-dialog :title="title" :visible.sync="onShow" @open="onOpen" :close-on-click-modal="false" @close="onClose" v-loading="ongetCarinfo" element-loading-text="加载数据...">
      <edit-cell :car-id="carid" :image-id="imageid" :car-info="carInfo" @car-save-done="onSaveDone" />
    </el-dialog>
  </div>
</template>
<script>
import EditCell from "./EditCell.vue";
import { getCarInfo } from "@/api";
import { createEmptyImagesList, createEmptyFeatrues } from "./models";

export default {
  name: "EmptyDialog",
  components: {
    EditCell
  },
  props: {
    visible: {
      type: Boolean,
      default: true
    },
    title: {
      type: String,
      default: "[详细]"
    },
    carid: {
      type: String,
      default: ""
    },
    imageid: {
      type: Number,
      default: -1
    }
  },
  data: function() {
    return {
      onShow: this.$props.visible,
      ongetCarinfo: false,
      carInfo: {
        images: [],
        imageid: 0,
        features: {}
      }
    };
  },
  methods: {
    onOpen() {
      this.ongetCarinfo = true;
      this.carInfo = {
        images: createEmptyImagesList(),
        imageid: 0,
        features: createEmptyFeatrues()
      };
      getCarInfo(this.$props.carid)
        .then(res => {
          if (res.success) {
            let tmpInfo = { features: "", imageid: -1, images: [] };
            tmpInfo.imageid = this.$props.imageid;
            if (
              res.data.features != undefined &&
              res.data.features.id != undefined
            ) {
              tmpInfo.features = res.data.features;
            }
            if (
              res.data.pictureOutputs != undefined &&
              res.data.pictureOutputs.length > 0
            ) {
              for (let i = 0; i < res.data.pictureOutputs.length; i++) {
                let tmp = res.data.pictureOutputs[i];
                tmpInfo.images.push({
                  name: tmp.name,
                  url: tmp.smallname,
                  bigurl: tmp.mediumname,
                  id: tmp.id
                });
              }
            }
            this.carInfo = tmpInfo;
            this.ongetCarinfo = false;
          } else {
            this.$message.warning(res.data);
            this.ongetCarinfo = false;
            this.onShow = false;
          }
        })
        .catch(e => {
          this.ongetCarinfo = false;
          console.log(e);
        });
    },
    onClose() {
      this.$emit("car:onclose", this.onShow);
    },
    onSaveDone() {
      this.carInfo = undefined;
      this.onShow = false;
      this.$emit("car:onclose", this.onShow);
    }
  },
  watch: {
    visible(val) {
      this.onShow = val;
    }
  }
};
</script>

<style lang="scss">
.car-edit-dialog {
  .el-dialog {
    height: 555px;
    width: 940px;
  }
  .el-dialog__body {
    padding: 0px 0px !important;
    margin-top: -5px;
    height:calc(100% - 16px);
  }
}
</style>




