<template>
    <icare-dialog :title="title" :visible="visible" @cancel="onCancel" @submit="onSubmit">
      <el-form slot="content" :model="formModel" :rules="rules" ref="formModel" label-width="100px" v-loading="isLoading">
          <el-form-item label="编码:" prop="code">
              <el-input prefix-icon="el-icon-edit" v-model="formModel.code" placeholder="请输入编码" auto-complete="off" :disabled="codeDisabled"></el-input>
          </el-form-item>
          <el-form-item label="名称:" prop="name">
              <el-input prefix-icon="el-icon-edit" v-model="formModel.name" placeholder="请输入名称" auto-complete="off"></el-input>
          </el-form-item>          
          <el-form-item label="描述:" prop="description">
              <el-input prefix-icon="el-icon-edit" v-model="formModel.description" placeholder="请输入描述"></el-input>
          </el-form-item>  
          <el-form-item label="后端角色:" prop="isBackend">
              <el-switch v-model="formModel.isBackend"></el-switch>
          </el-form-item>             
          <el-form-item label="是否启用:" prop="enabled">
              <el-switch v-model="formModel.enabled"></el-switch>
          </el-form-item>                       
      </el-form>
    </icare-dialog>  
</template>

<script>
import {addRole, updateRole} from '@/api'
import icareDialog from '@/components/base/Dialog'

export default {
    name: 'edit',
    components:{
        icareDialog
    },    
    props: ['propsVisible', 'propsEditInfo', 'propsEditMode'],
    data(){
        return {
            visible: false,
            title: "新增角色",
            rules: {
                code: [{required: true, message: '必填:编码', trigger: 'blur'}],
                name: [{required: true, message: '必填:名称', trigger: 'blur'}]
            },        
            formModel: {
                code: "",
                name: "",
                description: "",
                isBackend: false,
                enabled: true
            },
            isLoading: false          
        }
    },
    methods: {
        onCancel(){
            this.visible = false;
            this.clearForm();
            this.$emit("editClosed");
        },
        onSubmit(){
            var _this = this
            this.$refs['formModel'].validate((valid) => {
            if (!valid) {
                return false;
            }

            if (this.formModel.id) { //更新              
                this.isLoading = true;                         
                updateRole({ roleId: this.formModel.id, roleInfo: this.formModel }).then(res=> {
                    if (res.success) {
                        var data = res.data
                        _this.$message({type: "success", message: "操作成功"})
                        _this.doEditCompleted()
                    }
                    _this.isLoading = false;
                })
            } else { //添加
                _this.isLoading = true;
                addRole({roleInfo: _this.formModel}).then(res=> {
                    if (res.success) {
                        var data = res.data;
                        _this.$message({type: "success", message: "操作成功"});
                        _this.doEditCompleted()
                    }
                    _this.isLoading = false;                    
                })
                }          
            });
        },
        doEditCompleted(){
            this.visible = false;
            this.clearForm();
            this.$emit("editCompleted")
        },
        clearForm(){
            this.formModel = {
                code: "",
                name: "",
                description: "",
                isBackend: false,
                enabled: true
            }
            this.$refs['formModel'].resetFields();
        }            
    },    
    computed: {
        codeDisabled:function(){
            return this.propsEditMode==this.GLOBAL.formAction.EDIT;
        }  
    },
    watch: {
        propsEditInfo(value) {
            this.formModel = value
        },
        propsVisible(value){
            this.visible = value
        },
        propsEditMode(value){
            this.title = value == "add" ? "新增角色" : "编辑角色"
        }
    }
}
</script>





