<template >
  <div ref="bb" class="add-car-containner">
    <edit-cell/>
  </div>
</template>

<script>
import EditCell from "./EditCell.vue";

export default {
  name: "Add",
  components: {
    EditCell
  },
  methods: {
    resetSize:function(){
    this.$refs['bb'].style.height = document.documentElement.clientHeight-125 + "px"; 
    
    }
  },  
  mounted:function(){
    this.resetSize();    
    window.addEventListener("resize",this.resetSize);
  },
  beforeDestroy () {
    window.removeEventListener("resize",this.resetSize);
  }
};
</script>

<style>
.add-car-containner {
  padding: 0px 0px;
  margin: 0px 0px;
  overflow-y: hidden;
  width: 1000px;
}
</style>





