<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item label="登录人员">
          <!-- <el-select  v-model="username" size="mini" filterable placeholder="请输入人员姓名">
                        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select> -->
          <el-input clearable prefix-icon="el-icon-search" size="mini" placeholder="通过关键字搜索,记得回车哦..." v-model="filters.keywords" @change="keywordsChange" @keyup.enter.native="doSearch"></el-input>
        </el-form-item>
        <el-form-item>
          <span>时间范围：</span>
          <el-date-picker v-model="filters.datetimes" size="mini" type="datetimerange" range-separator="至" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" size="mini" @click="doSearch">查询</el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="mini" @click="doReset">重置</el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table highlight-current-row border stripe size="mini" :data="items" v-loading="listLoading" @selection-change="selectionChange">
      <el-table-column type="index" label="序号" :index="calcRowNumber" width="180"></el-table-column>
      <el-table-column prop="userName" label="登录人员" width="180"></el-table-column>
      <el-table-column prop="operation" label="操作内容" width="180"></el-table-column>
      <el-table-column label="操作时间" width="180">
        <template slot-scope="scope">{{ scope.row.operationTime | formatDateTime}}</template>
      </el-table-column>      
      <el-table-column></el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar">
      <el-pagination background style="float:right;" :page-size="pageSize" :current-page="pageIndex" @current-change="doTurnPage" layout="prev, pager, next" :total="totalCount"></el-pagination>
    </el-col>
  </section>

</template>

<script>
import { getLogs } from "@/api";

//import edit from './Edit.vue'
//import permission from './Permission.vue'

export default {
  // components:{
  //   edit,
  //   permission
  // },
  data() {
    return {
      filters: { keywords: "", datetimes: null },
      items: [],
      pageSize: this.GLOBAL.settings.common.pageSize,
      pageIndex: 1,
      totalCount: -1,
      selectedItems: [],
      listLoading: false,

      editMode: this.GLOBAL.formAction.ADD,
      editFormVisible: false,
      editInfo: {},

      permissionRole: null,
      permissionFormVisible: false
    };
  },
  mounted: function() {
    this.init();
  },
  computed: {},
  methods: {
    init() {
      this.loadItems();
    },
    loadItems() {
      var _this = this;
      this.listLoading = true;
      var beginTime = "",
        endTime = "";
      if (this.filters.datetimes != null) {
        beginTime = new Date(this.filters.datetimes[0]).toDateTimeString();
        endTime = new Date(this.filters.datetimes[1]).toDateTimeString();
      }

      getLogs({
        keywords: this.filters.keywords,
        beginTime: beginTime,
        endTime: endTime,
        pageIndex: this.pageIndex,
        pageSize: this.pageSize
      }).then(res => {
        this.listLoading = false;
        if (res.success) {
          var data = res.data;
          _this.items = data.items;
          _this.totalCount = data.totalCount;
        }
      });
    },
    hasRecords() {
      return items.length > 0;
    },
    keywordsChange(val) {
      if (val == "") {
        this.loadItems();
      }
    },
    selectionChange(items) {
      this.selectedItems = items;
    },
    doSearch() {
      this.loadItems();
    },
    doReset(){
      this.filters.keywords = "";
      this.filters.datetimes = null;
    },
    doTurnPage(currentChange) {
      this.pageIndex = currentChange;
      this.loadItems();
    },
    doDelete(row) {
      this.$confirm("此操作将永久删除[" + row.name + "], 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.deleteItems(row.id);
        })
        .catch(() => {});
    },
    doBatchDelete() {
      this.$confirm(
        "此操作将删除[" + this.selectedItems.length + "]条数据, 是否继续?",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      )
        .then(() => {
          var ids = "";
          for (var i = 0; i < this.selectedItems.length; i++) {
            ids += this.selectedItems[i].id + ",";
          }
          this.deleteItems(ids);
        })
        .catch(() => {});
    },
    deleteItems(ids) {
      this.listLoading = true;
      var _this = this;
      deleteRoles({ ids: ids }).then(res => {
        _this.listLoading = false;
        if (res.success) {
          _this.$message({ type: "success", message: "操作成功" });
          _this.loadItems();
        }
      });
    },

    showAddForm() {
      this.editMode = this.GLOBAL.formAction.ADD;
      this.editFormVisible = true;
    },
    showEditForm(row) {
      this.editMode = this.GLOBAL.formAction.EDIT;
      this.editInfo = this.deepCopy(row);
      this.editFormVisible = true;
    },
    onEditFormClosed() {
      this.editFormVisible = false;
    },
    onEditFormCompleted() {
      this.editFormVisible = false;
      this.loadItems();
    },

    showPermissionForm(row) {
      this.permissionRole = row;
      this.permissionFormVisible = true;
    },
    onPermissionFormClosed() {
      this.permissionFormVisible = false;
    },
    onPermissionFormCompleted() {
      this.permissionFormVisible = false;
    },
    calcRowNumber(index){      
      return (this.pageIndex - 1) * this.pageSize + index + 1
    }
  }
};
</script>



