<template>
  <div class="img-upload">
    <div v-if="imageUrl != null && imageUrl.url != ''">
      <div :class="[this.$props.selectedIndex==this.$props.index?'img-selected':'' ,'img-containner']">
        <img class="img-content" :src="imageUrl.url" @click="handleSelect" :onerror='"this.src=\"" + require("../../assets/images/default.png") + "\""'/>
        <i @click="handleRemove" class="el-icon-delete img-btn-delete"></i>
      </div>
      <p class="img-title" v-text="imageUrl.name"></p>
    </div>
    <el-upload v-else class="img-upload" action="/api/files" :auto-upload=false :on-change="handleSelected" list-type="picture-card" :show-file-list="false" :objectBind="index" accept="image/png, image/jpeg" :on-remove="handleRemove">
      <i class="el-icon-plus"></i>
    </el-upload>
  </div>
</template>

<script>
export default {
  props: ["imageUrl", "index", "selectedIndex"],
  data: function() {
    return {
      image: this.$props.imageUrl
    };
  },
  methods: {
    handleSelected(file) {
      if (file != null) {
        var tmpFile = {};
        tmpFile.name = file.name;
        tmpFile.url = file.url;
        this.image = tmpFile;
        tmpFile = null;
        this.$emit("add", file, this.$props.index, this.image);
      }
    },
    handleRemove() {
      if (this.$props.imageUrl != null) {
        this.$emit("deleted", this.$props.imageUrl, this.$props.index);
      }
    },
    handleSelect() {
      if (this.$props.imageUrl != null) {
        this.$emit("selected", this.$props.imageUrl, this.$props.index);
      }
    }
  }
};
</script>

<style>
.img-upload .el-upload--picture-card {
  width: 100%;
  height: 71px;
  border: 1px solid #e5e7f7;
  border-radius: 0px;
}

.img-containner {
  position: relative;
  width: 100%;
  height: 71px;
  display: block;
}

.img-content {
  width: 100%;
  height: 71px;
}

.img-containner:hover .img-btn-delete {
  display: block;
}

.el-upload--picture-card {
  line-height: 72px;
}

.img-selected {
  border: 1px solid #1b90f7;
}
.img-btn-delete {
  position: absolute;
  bottom: 2px;
  right: 2px;
  display: none;
}

.img-title {
  line-height: 15px;
  font-family: "MicrosoftYahei";
  font-size: 12px;
  color: #062238;
  text-align: left;
  width: 100%;
  margin: 5px auto auto auto;
  white-space: normal;
  word-wrap: break-word;
  padding: 0 0 0 0;
}
</style>



