<template>
  <el-row class="car-add-containner" v-loading="onLoading" element-loading-text="正在保存...">
    <el-col :span="13" class="car-image-selector car-add-cell-col">
      <ImageSelector ref="child" :image-info="selectedCarImages" v-on:updateImages="updateImages" v-on:updateMarks="updateMarks" />
    </el-col>
    <el-col :span="9" class="car-add-cell-col">
      <FeatureSelector v-if="showF" @feature:changed="updateFeatures" @feature:reset="resetFeatures" :features-info="slectedCarInfo" :rules="validatesRules" />
      <el-button class="btn-save" type="primary" size="small" @click="handleSave">保存</el-button>
    </el-col>
  </el-row>
</template>

<script>
import FeatureSelector from "../../common/FeatureSelector.vue";
import ImageSelector from "../../common/ImageSelector.vue";

import { createCar, uploadImages, getCarExists } from "@/api";

import { createEmptyImagesList, createEmptyFeatrues } from "./models";

export default {
  name: "EditCell",
  components: {
    FeatureSelector,
    ImageSelector
  },

  props: ["carInfo"],

  data: function() {
    return {
      slectedCarInfo: {
        images: createEmptyImagesList(),
        imageid: 0,
        features: createEmptyFeatrues()
      },
      selectedCarImages: {
        images: createEmptyImagesList(),
        imageid: 0
      },
      upmarks: {},
      upImages: [],
      deleteImageIds: [],
      onLoading: false,
      validatesRules: {
        brand: false,
        modelyear: false,
        series: false,
        model: false
      },
      showF: false
    };
  },
  mounted() {
    setTimeout(
      function() {
        this.showF = true;
      }.bind(this),
      200
    );
  },
  watch: {
    carInfo(val) {
      console.log("watch carInfo:::" + val);
      console.log(val);
      if (val != undefined) {
        this.slectedCarInfo = val;
        this.selectedCarImages = { images: val.images, imageid: val.imageid };
      }
    }
  },
  methods: {
    updateMarks(marks, name) {
      let ups = [];
      if (marks !== undefined && marks.length > 0) {
        marks.map(item => {
          ups.push({
            top: item.top,
            left: item.left,
            width: item.width,
            height: item.height,
            originalWidth: item.originalWidth,
            originalHeight: item.originalHeight
          });
        });
      }
      this.upmarks[name] = ups;
      console.log(this.upmarks);
    },

    updateImages(images) {
      this.upImages = images;
      console.log(images);
    },

    updateFeatures(features, propertyName) {
      this.slectedCarInfo.features = features;
      if (
        this.validatesRules.hasOwnProperty(propertyName) &&
        this.slectedCarInfo.features.hasOwnProperty(propertyName)
      ) {
        console.log(
          "select P :" +
            propertyName +
            "this.slectedCarInfo.features[propertyName] =null :" +
            this.slectedCarInfo.features[propertyName] !=
            ""
        );
        this.validatesRules[propertyName] =
          this.slectedCarInfo.features[propertyName] == "";
      }
      console.log(features);
    },
    resetFeatures() {
      this.$set(this.slectedCarInfo, "features", createEmptyFeatrues());
    },
    onSaveDone() {
      this.onLoading = false;
      this.$message({ type: "success", message: "操作完成", duration: 1000 });
      //this.$set(this.selectedCarImages,"images", createEmptyImagesList());
      if (this.$props.carInfo != undefined) {
        this.$emit("car-save-done");
      } else {
        this.slectedCarInfo.features.brand = "";
        this.slectedCarInfo.features.modelyear = "";
        this.slectedCarInfo.features.series = "";
        this.slectedCarInfo.features.model = "";
        this.slectedCarInfo.features.id = "";
      }

      this.$root.$emit("car-saved-done");
    },
    handleCheckRules() {
      let validateRule = false;
      let ruleNames = Object.getOwnPropertyNames(this.validatesRules).filter(
        rule => {
          return (
            this.validatesRules.hasOwnProperty(rule) &&
            this.slectedCarInfo.features.hasOwnProperty(rule) &&
            rule.substring(0, 2) !== "__"
          );
        }
      );
      if (ruleNames != undefined && ruleNames.length > 0) {
        ruleNames.forEach(rule => {
          this.validatesRules[rule] = this.slectedCarInfo.features[rule] == "";
          if (!validateRule) validateRule = this.validatesRules[rule];
          console.log(
            "validateRule" + validateRule + "" + this.validatesRules[rule]
          );
        });
      }
      return validateRule;
    },
    handleSave() {
      if (this.handleCheckRules()) {
        this.$message({ type: "warning", message: "请输入必填信息！" });
        return;
      }
      this.onLoading = true;
      getCarExists(
        this.slectedCarInfo.features.brand,
        this.slectedCarInfo.features.series,
        this.slectedCarInfo.features.modelyear,
        this.slectedCarInfo.features.model
      ).then(res => {
        this.onLoading = false;
        if (res.data != "" && this.slectedCarInfo.features.id != res.data) {
          this.$confirm("数据已存在，是否覆盖?", "提示", {
            confirmButtonText: "是",
            cancelButtonText: "否",
            type: "warning"
          })
            .then(() => {
              //this.slectedCarInfo.features.id = res.data;
              this.doSave(true);
            })
            .catch(() => {
              //取消
            });
        } else {
          this.doSave(false);
        }
      });
    },
    doSave(requireDelete) {
      this.onLoading = true;
      //上传特征值
      createCar(this.slectedCarInfo.features, requireDelete)
        .then(res => {
          if (res.success) {
            let id = res.data;
            if (this.upImages.length > 0) {
              let formData = new FormData();
              let parameters;
              this.upImages.map((item, index) => {
                formData.append("files", item.raw);
              });
              formData.append(
                "parameters",
                new Blob([JSON.stringify(this.upmarks)], {
                  type: "application/json"
                })
              );
              formData.append(
                "imgeIds",
                new Blob([JSON.stringify(this.deleteImageIds)], {
                  type: "application/json"
                })
              );

              uploadImages(formData, "/images/" + id)
                .then(res => {
                  this.onSaveDone();
                })
                .catch(e => {
                  this.onLoading = false;
                  this.$message({
                    type: "error",
                    message: "保存图片失败！",
                    duration: 1000
                  });
                });
            } else {
              this.onSaveDone();
            }
          } else {
            this.onLoading = false;
          }
        })
        .catch(e => {
          this.onLoading = false;
          this.$message({
            type: "error",
            message: "保存特征失败！",
            duration: 1000
          });
        });
    }
  }
};
</script>

<style>
.car-add-containner {
  height: 100%;
  padding: 0px 0px;
  margin: 0px 0px;
  overflow-y: hidden;
  width: 100%;
}

.car-add-cell-col {
  height: calc(100% - 40px);
  margin-top: 20px;
  margin-bottom: 20px;
}

.car-image-selector {
  margin-right: 20px;
  margin-left: 20px;
  padding: 0px 0px !important;
}

.btn-save {
  float: bottom;
  position: relative;
  bottom: 40px;
  margin: auto auto;
  left: 50%;
  transform: translateX(-50%);
}
</style>





